import React from 'react';
import '../../../components/style.scss';
import Helmet from '../../../components/helmet';
import Navbar from '../../../components/navbar';
import Header from '../../../components/header';
import Footer from '../../../components/footer';
import { Link } from "gatsby"

import Roles1 from '../../../images/img/roles1.png'
import Roles2 from '../../../images/img/roles2.png'

const PodcastOverviewPage= () => (
<html>
			<Helmet>
		 </Helmet>
		 <Navbar />
    <section className="section">
	    	<div class="container" >
	    		<h1 className="title is-size-4-mobile" id="point">STENT Overview</h1>
	    				<hr></hr>

	      </div>
				<section className="section">
					<div class="container">
								<h1 className="title is-size-4-mobile" id="ideal">The Ideal Collaborative Research Project</h1>
										<hr></hr>
								<h2 className="subtitle is-size-5-mobile">
								There is no perfect study, but for the purposes of a research collaborative project, certain types of projects have been shown to work well in the early stages of the collaborative. This may help trainees develop their own ideas:
								</h2>
								<h2 className="subtitle is-size-5-mobile">
								<strong> Short term data collection </strong>(e.g. primary outcome at 1 month or less). This is because the shorter the length of the study, the easier it is to engage trainees to carry out the project and complete data collection before they move onto their next job
								</h2>
								<h2 className="subtitle is-size-5-mobile">
								<strong>Common pathologies</strong>. The more common the problem being investigated, the easier it will be to recruit adequate numbers of patients across many centres.
								</h2>
								<h2 className="subtitle is-size-5-mobile">
								<strong>Observational studies</strong>.i.e. where clinical practice is not being changed as part of the study. Sometimes termed cohort studies. Sometimes this data can be obtained from audit. The advantage of these types of studies is that ethical approval and funding may not always be required. To produce higher levels of evidence, interventional studies (e.g. randomized controlled trials) are important but observational studies still have an important role, particularly in generating hypotheses.
								</h2>
								<h2 className="subtitle is-size-5-mobile">
								<strong>Interventional studies</strong> – if randomising patients to one arm or another, ensure that the majority of clinicians have <strong>“equipoise”</strong>. I.e. that at each centre, the lead clinician has genuine uncertainty over whether the interventions under investigation are better than one another and that they would be happy to randomise patients to either of the interventions. If many clinicians have strong preferences for one intervention or another, the study will not recruit well.
								</h2>
								<h2 className="subtitle is-size-5-mobile">
								For interventional studies – the <strong>simpler the intervention </strong>and the easier it will be to administer as part of clinical practice, the more likely the study will be successful. Studies which require significant deviation from clinical practice and require specialist skills not usually available are more difficult.
								</h2>
								<h2 className="subtitle is-size-5-mobile">
								Once the collaborative has successfully delivered observational studies, there can be a greater emphasis on interventional studies and once interventional studies have been delivered, there can be a greater emphasis on complex interventions. However if the right idea and study is suggested, whether it is interventional or observational, it will be taken forward.
								</h2>
								<h2 className="subtitle is-size-5-mobile"> We invite anyone with a keen interest in Urology to get in touch (via <a href="#">admin@bursturology.com</a>) to discuss the potential of a research collaboration. Below, we have described ways you can collaborate with us. <a href="https://docs.google.com/document/d/1y_lBMWzfkzVrNJP0TLzNDaVjx6KvDSd_5rwj-NzCoWw/edit?usp=sharing">Please use the project proposal proforma accessible here to help structure your proposal.</a>
								</h2>
								<div class="container has-text-centered" >
								<figure className="media-content is-size-4-mobile">
										<img id="imgcentre2" src={Roles1}></img>
								</figure>
								<br></br>
								<figure className="media-content is-size-4-mobile">
										<img id="imgcentre2" src={Roles2}></img>
								</figure>
								</div>
						</div>
				</section>

				</section>

		<Footer />
		</html>
);

